export default{
	data:function(){
		return {
			pageData:{},
			pageLoad:false
		}
	},
	onReady:function(){
		
	},
	onLoad:function(){
		this.getPage();
	},
	methods:{
		goCbd:function(cbdid){
			uni.navigateTo({
				url:"../b2b_cbd/show?cbdid="+cbdid
			})
		},
		getPage:function(){
			var that=this;
			this.app.get({
				url:this.app.apiHost+"/module.php?m=b2b_cbd",
				success:function(res){
					if(res.data.seo!=null){
						uni.setNavigationBarTitle({
							title:res.data.seo.title
						})
					}
					that.pageData=res.data;
					that.pageLoad=true;
				}
			})
		}
	}
}
